.intro {   
    color: #ffffff;         
    font-family: 'me-r';
    font-size: 30px;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro1 {   
    color: #ffffff;         
    font-family: 'me-r';
    font-size: 30px;
    margin-top: -50vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.paragraph {
    color: #ffffff;
    font-family: 'product-sans';
    font-size: 145px;
    margin: 0;
    line-height: 160px;    
    /* border: 3px solid yellow; */
    width: 70vw;
    height: 110vh; 
    position: relative;
}

.innerText {
    position: absolute;
    right: 0;
    bottom: 0;
    width: fit-content;
    text-align: left;
    /* border: 3px solid yellow; */
}


.back {
    position: absolute;
    width: 90vw;
    height: 100vh;
    z-index: -1;
}

.backTexts {
    position: absolute;
    z-index: -50;
}

.backTextUp{
    color: #1f1f1f;
    font-size: 580px;
    font-family: 'me-r';
    display: inline-block;
    margin: 0;
    width: auto;
    height: 50vh;
    line-height: 60vh;
}

.backTextDown{
    color: #1f1f1f;

    font-size: 580px;
    margin: 0;
    width: fit-content;
    height: fit-content;
    font-family: 'me-r';
    width: 100vw;
    height: 40vw;
}

.name {
    color: #0097A7;

}

.image-cr {
    color: #FF5252;
}

.points {
    font-family: 'me-r';
    color: #A8AAAA;
    writing-mode:vertical-rl;
    margin-left: 20vh;
    margin-top: -5vh;
    width: fit-content;
    height: fit-content;
    transform: rotate(180deg);
    
}

.pointsOuter {
    width: fit-content;
    height: fit-content;
    padding-top: 58vh;
    padding-left: 5vh;  ;
}

.reach {
    font-family: 'me-r';
    color: #A8AAAA;
    font-size: 18px;
    writing-mode:vertical-rl;
    width: fit-content;
    height: fit-content;
    transform: rotate(180deg);
    margin-top: -142vh;
    text-align: right;
    z-index: 50;
}

.tab { 
    margin-top: 40px; 

} 

.reachOuter {
    margin-left: 187vh;
    width: fit-content;
    height: fit-content;
    float: right; 
    padding-top: 80vh

}